import { React,  useEffect, useContext } from 'react';
import Header from './components/Header';
import Body from './components/Body';
import './home.css';
import {useParams} from "react-router-dom";
import cloneContext from './Context/CloneContext';
import Login from './Login';

const Home = () => {

  const {id}=useParams();
  const {state,changeUser,addressState,changeaddress,cartState,changeuserCart,Validate,changeVali}=useContext(cloneContext);
  const {validate}=Validate


 useEffect(()=>{
  changeUser(id)
  },[])

if(validate==false)
return <Login />

  return (
    <div className='amazon-home'>
      <Header />
      <Body />
    </div>
  )
}

export default Home
