import { React, useState, useContext } from 'react';
import './profile.css';
import cloneContext from './Context/CloneContext';
import axios from 'axios'
import Header from './components/Header';
import Login from './Login';

const Profile = () => {
    const {state,changeUser,addressState,changeaddress,cartState,changeuserCart,Namestate,changeName,Passkeystate,changePasskey,Validate,changeVali} = useContext(cloneContext)
    const {user_cart}=cartState
    const{username}=state
    const {user_Name}=Namestate;
    const {user_address}=addressState
    const {user_Passkey}=Passkeystate

    const [Edit_passward,setEdit_password]=useState(false)
    const [Edit_name,setEdit_name]=useState(false)
    const [Edit_address,setEdit_address]=useState(false)

    const [Password_input, setInput_password] = useState("");
    const [Name_input, setInput_Name] = useState("");
    const [Address_input, setInput_Address] = useState("");
    const {validate}=Validate
    
    async function Edit_database_password()
    {
        if(Password_input!='')
        {
       
        axios.post(`/api/changePassword/${username}/${Password_input}`,{success:true})
        changePasskey(Password_input)
        setInput_password('')
        }
        setEdit_password(!Edit_passward)

    }
    function Edit_database_name()
    {
        if(Name_input!='')
        {
        
        axios.post(`/api/changeName/${username}/${Name_input}`,{success:true})
        changeName(Name_input)
        setInput_Name('')
        }

        setEdit_name(!Edit_name)

    }
    function Edit_database_address()
    {
        if(Address_input!='')
        {
        
        axios.post(`/api/changeAddress/${username}/${Address_input}`,{success:true}).then(()=>{

        })
        changeaddress(Address_input)
        setInput_Address('')
        }

        setEdit_address(!Edit_address)
        

    }

    if(validate==false) return <Login />

  return (
    <div className='Profile-container'>
    <Header />
    <div className='Info-container'>
        <table className='Info-section'>

            <tr className='Info-block'>
                <td className='Info_title'>
                Username: 
                </td>
                <td>
                {username}
                </td>
            </tr>
            <tr className='Info-block normal_tr'>
                <td className='Info_title'>
                Password: 
                </td>
                <td className='Info_data'>
                
                <span className={`${Edit_passward&&"hide_it"}  data`} >{user_Passkey}</span>
                <input type='text' className={`change_size ${!Edit_passward&&"hide_it"}`} onChange={(e)=>setInput_password(e.target.value)}/>
                <span className='small_td'>
                <button className={`${Edit_passward&&"hide_it"} `} onClick={()=>{setEdit_password(!Edit_passward)}}>Edit</button>
                <button className={`change_size ${!Edit_passward&&"hide_it"}`} onClick={()=>{Edit_database_password()}}>ok</button>
                </span>

                </td>
            </tr>
            <tr className='Info-block normal_tr'>
                <td className='Info_title'>
                Name:
                </td>
                <td className='Info_data'>
                
                <span className={`${Edit_name&&"hide_it"}  data`} >{user_Name}</span>
                <input type='text' className={`change_size ${!Edit_name&&"hide_it"}`} onChange={(e)=>setInput_Name(e.target.value)}/>
                <span className='small_td'>
                <button className={`${Edit_name&&"hide_it"} `} onClick={()=>{setEdit_name(!Edit_name)}}>Edit</button>
                <button className={` ${!Edit_name&&"hide_it"}`} onClick={()=>{Edit_database_name()}}>ok</button>
                </span>

                </td>
            </tr>
            <tr className='Info-block normal_tr'>
                <td className='Info_title'>
                Address:
                </td>
                <td className='Info_data'>
                
                <span className={`${Edit_address&&"hide_it"}  data`} >{user_address}</span>
                <input type='text' className={`change_size ${!Edit_address&&"hide_it"}`} onChange={(e)=>setInput_Address(e.target.value)}/>
                <span className='small_td'>
                <button className={`${Edit_address&&"hide_it"}`} onClick={()=>{setEdit_address(!Edit_address)}}>Edit</button>
                <button className={` ${!Edit_address&&"hide_it"}`} onClick={()=>{Edit_database_address()}}>ok</button>
                </span>


                </td>
            </tr>
            
        </table>
    </div>
      
    </div>
  )
}

export default Profile
