import CloneContext from "./CloneContext";
import {React,useState,useEffect} from 'react'

const CloneState = (props) => {
    const [vali,setVali]=useState(false)
    const Validate={validate:vali}

    const [user,setUser]=useState("")
    const state={username:user}

    const [name,setName]=useState("")
    const Namestate={user_Name:name}

    const [passkey,setPasskey]=useState("")
    const Passkeystate={user_Passkey:passkey}

    const [address,setAddress]=useState("")
    const addressState={user_address:address}

    
    const [userCart,setCart]=useState([])
    const cartState={user_cart:userCart}
    
    function changeVali(newVali)
    {
        setVali(newVali)

    }
    function changeUser(newUser)
    {
        setUser(newUser)

    }
    function changeaddress(newaddress)
    {
        setAddress(newaddress)
    }
    function changeuserCart(newcart)
    {
        setCart(newcart)
    }
    function changeName(newname)
    {
        setName(newname)
    }
    function changePasskey(newpass)
    {
        setPasskey(newpass)
    }
  return (
    <CloneContext.Provider value={{state,changeUser,addressState,changeaddress,cartState,changeuserCart,Namestate,changeName,Passkeystate,changePasskey,Validate,changeVali}}>
      {props.children}
      </CloneContext.Provider>
  )
}

export default CloneState
