import { React, useContext } from 'react';
import TrendingViodecard from './components/TrendingViodecard';
import cloneContext from './Context/CloneContext';
import './usercart.css'
import Header from './components/Header';
import Login from './Login';

const UserCart = () => {

    const {state,changeUser,addressState,changeaddress,cartState,changeuserCart,Validate,changeVali}=useContext(cloneContext);
    const {user_cart}=cartState;
    let total_price=0;
    const {validate}=Validate

user_cart?.map((product)=>{total_price+=Number(product?.price?.replace('$', ''))})

total_price='$'+total_price

if(validate==false) return <Login />
  return (
    <>
    <Header />
    <div className='Cart-page'>
    <div className='Cart-container'>
      {user_cart?.map((product)=><span className='Cart-block'><TrendingViodecard  image1={product.image}  title={product.title} price={product.price} asin={product.asin} /></span> )}
    </div>
    <div className='Total-price-section'>
    <div className='sub-total'>
        <p>Subtotal ({user_cart.length}): {total_price.includes('.')?total_price.substring(0,total_price.indexOf(".")):total_price}</p>
        <button>Proceed to Checkout</button>
    </div>
    </div>
    </div>
    </>
  )
}

export default UserCart
