import { React, useState, useEffect,useContext } from 'react';
import './productpage.css';
import axios from 'axios'
import Header from './components/Header'
import { useParams } from 'react-router-dom';
import cloneContext from './Context/CloneContext';



const ProductPage = () => {
    const [displayobj, setdisplayobj] = useState({})
    const [displayWaiting, setDisplayWaiting] = useState(true)

    const { keyword } = useParams()

    const {state,changeUser,addressState,changeaddress,cartState,changeuserCart} = useContext(cloneContext)
    const {user_cart}=cartState
    const {username}=state



    // const options = {
    //     method: 'GET',
    //     url: 'https://amazon23.p.rapidapi.com/product-details',
    //     params: {asin: keyword, country: 'US'},
    //     headers: {
    //       'X-RapidAPI-Key': '3b7721c1ebmshfc98c1f8b026b76p168edcjsn7fc35a4b686a',
    //       'X-RapidAPI-Host': 'amazon23.p.rapidapi.com'
    //     }
    //   };

    const options = {
      method: 'GET',
      url: 'https://amazon23.p.rapidapi.com/product-details',
      params: {asin: keyword, country: 'US'},
      headers: {
        'X-RapidAPI-Key': 'f7f4ea22cdmshc78e2b4f79bc64ep1fa994jsncd9aae68bc9e',
        'X-RapidAPI-Host': 'amazon23.p.rapidapi.com'
      }
    };



      
 useEffect(()=>{

  setDisplayWaiting(true)


    axios.request(options).then(function (response) {
        
        setdisplayobj(response.data.result[0])
        setDisplayWaiting(false)
    }).catch(function (error) {
        console.error(error);
    });
    
  }, [keyword])

  async function AddToDatabaseCart( )
  {
    
    const Product_to_add={
      title:title,
      image:image,
      price:price,
      asin:keyword
      
    }
    changeuserCart([...user_cart,Product_to_add])

   await axios.post(`/api/addToCart/${username}`,{Product:Product_to_add}).then(()=>{

   })

  }
  

let price='$'+displayobj.price?.current_price
let image=displayobj.main_image;
let title=displayobj.title


if(displayWaiting)
return<div className='loader-container'>
<div className="spinner"></div>
</div>

  return (
    <div className='product-page'>
      <Header />
      <div className='product-container'>
      <div className='upper-container'>
      <div className='upper-container-image'>
        <img className='main-image' src={image} />
      </div>
      <div className='upper-container-data'>
      <p className='upper-container-data-title'>{title}</p>
      <p className='upper-container-data-des'>{displayobj?.description?.substring(0,600)}</p>
      <p className='price-data'>{price}</p>
      <div className='product-page-btns'>
           <button className='buy-btn'>Buy Now</button>
           <button className='add-cart-btn' onClick={()=>{AddToDatabaseCart()}} >Add To Cart</button>
   
      </div>
      </div>
      </div>
      <div className='container-features'>
      <h4 className='feature-heading'>Features</h4>
        {displayobj?.feature_bullets?.map((feature)=>{if(feature!='') return<div className='feature-block'>- {feature}</div>})}
      </div>
      <div className='container-images'>
      <h4 className='feature-heading'>Images</h4>
      {displayobj?.images?.map((imgUrl) =><img src={imgUrl} />)}
      </div>

      </div>
    </div>
  )
}

export default ProductPage
