import { React, useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from "axios";
import cloneContext from '../Context/CloneContext';


import './trendingvideocard.css'
const TrendingViodecard = ({ image1, title, price, asin }) => {

    const {state,changeUser,addressState,changeaddress,cartState,changeuserCart}=useContext(cloneContext);
    const {user_cart}=cartState;
    const{username}=state
    const navigate = useNavigate();
    
   async function remove_from_cart()
    {
        let arr=[]
        arr = user_cart.filter(item => item.asin !== asin)
        changeuserCart(arr)

        await axios.post(`/api/removeFromCart/${username}`,{asin:asin}).then(()=>{
            
        })

    }
    

   
 
    return (
        <div className='trending_card'>
            <div className='trending_thumbnail' onClick={()=>navigate(`/product/${asin}`)}>
                <img src={image1} />
            </div>
            <div className='trending_text'>
                <h3 onClick={()=>navigate(`/product/${asin}`)}>{title}</h3>
                <p>{price}</p>
           <div className='page-btns'>
           <button className='check-btn'>Check Out</button>
           <button className='remove-btn' onClick={remove_from_cart} >Remove</button>
           </div>
           </div>
        </div>
    )
}

export default TrendingViodecard
