import { React, useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import cloneContext from '../Context/CloneContext';
import axios from 'axios'
import './searchcard.css';


const SearchCard = ({image1,title,price,asin}) => {
  const {state,changeUser,addressState,changeaddress,cartState,changeuserCart} = useContext(cloneContext)
  const {user_cart}=cartState
  const{username}=state
  const navigate = useNavigate();

  price='$'+price
 
 async function AddToDatabaseCart( )
  {
    
    const Product_to_add={
      title:title,
      image:image1,
      price:price,
      asin:asin
      
    }
    changeuserCart([...user_cart,Product_to_add])

   await axios.post(`/api/addToCart/${username}`,{Product:Product_to_add}).then(()=>{

   })

  }
  return (
    <div className='cmt-card'>
    <div className='inner-div' onClick={()=>navigate(`/product/${asin}`)}>
    <img src={image1} />
    
    <div className='cmt-thumbnail'>
      <h3 className='cmt-thumbnail-title'>{title}</h3>
     
     <p className='price'>{price}</p>
     
    </div>
 
    
  </div>
  <button className='add-btn' onClick={()=>{AddToDatabaseCart();}}>Add to Cart</button>
  </div>
  
 
  )
}

export default SearchCard
