import { React, useState, useEffect } from 'react';
import SearchCard from './components/SearchCard';
import './searchpage.css';
import { useParams, useNavigate } from 'react-router-dom';
import axios from 'axios'
import Header from './components/Header';


const SearchPage = () => {

  const navigate = useNavigate();
  const { keyword } = useParams()
  const [displayArray, setDisplayArray] = useState([])
  const [displayWaiting, setDisplayWaiting] = useState(true)


  // const options = {
  //   method: 'GET',
  //   url: 'https://amazon23.p.rapidapi.com/product-search',
  //   params: {query: keyword, country: 'US'},
  //   headers: {
  //     'X-RapidAPI-Key': '3b7721c1ebmshfc98c1f8b026b76p168edcjsn7fc35a4b686a',
  //     'X-RapidAPI-Host': 'amazon23.p.rapidapi.com'
  //   }
  // };

  const options = {
    method: 'GET',
    url: 'https://amazon23.p.rapidapi.com/product-search',
    params: {query: keyword, country: 'US'},
    headers: {
      'X-RapidAPI-Key': 'f7f4ea22cdmshc78e2b4f79bc64ep1fa994jsncd9aae68bc9e',
      'X-RapidAPI-Host': 'amazon23.p.rapidapi.com'
    }
  };
  

 
  
  useEffect(()=>{


    setDisplayWaiting(true)

    axios.request(options).then(function (response) {
      setDisplayArray(response.data.result)
      setDisplayWaiting(false)
    }).catch(function (error) {
      console.error(error);
    });



  }, [keyword])

  if(displayWaiting)
  return<div className='loader-container'>
  <div className="spinner"></div>
</div>
  
  return (
    <div>
   <Header />
    <div className='Commented_videos'>
     {
      displayArray?displayArray.map((product)=><div  ><SearchCard image1={product.thumbnail} title={product.title} price={product?.price?.current_price} asin={product.asin} /></div>):null
      }
    
    </div>
    </div>
  )
}

export default SearchPage
