import { React,  useContext } from 'react';
import './productblock.css';
import cloneContext from '../Context/CloneContext';
import axios from 'axios'
import { useNavigate } from 'react-router-dom';


const ProductBlock = ({title,image,price,asin}) => {
  const {state,changeUser,addressState,changeaddress,cartState,changeuserCart} = useContext(cloneContext)
  const {user_cart}=cartState
  const{username}=state
  const navigate = useNavigate();

  
 
 async function AddToDatabaseCart( )
  {
    
    const Product_to_add={
      title:title,
      image:image,
      price:price,
      asin:asin
      
    }

   axios.post(`/api/addToCart/${username}`,{Product:Product_to_add})

   changeuserCart([...user_cart,Product_to_add])
  }

  return (
    <div className='product-block'>
      <p onClick={()=>{navigate(`/product/${asin}`)}}>{title}</p>
      <img onClick={()=>{navigate(`/product/${asin}`)}} src={image} />
      <p onClick={()=>{navigate(`/product/${asin}`)}} className='price'>{price}</p>
      <button onClick={()=>{AddToDatabaseCart();}}>Add to Cart</button>
    </div>
  )
}

export default ProductBlock
