import { React, useState, useEffect, useContext } from 'react';
import './header.css'
import {useNavigate} from 'react-router-dom'
import cloneContext from '../Context/CloneContext';
import axios from "axios";
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import LogoutIcon from '@mui/icons-material/Logout';
import HomeIcon from '@mui/icons-material/Home';


const Header = () => {
  const {state,changeUser,addressState,changeaddress,cartState,changeuserCart,Namestate,changeName,Passkeystate,changePasskey,Validate,changeVali} = useContext(cloneContext)
  const {user_cart}=cartState
  const {username}=state
  const [input_text, setInput_text] = useState("");
  const navigate=useNavigate();
 


  useEffect(()=>{
   
   axios.request(`/api/Get_user_data/${username}`).then((response)=>{
    changeuserCart(response.data[0].user_cart)
    changeName(response.data[0].name)
    changeaddress(response.data[0].address)
    changePasskey(response.data[0].password)
    

  
  }).catch((error)=>{
  console.log(error)
  })
  },[username])

  function navigate_user(){
    navigate(`/search/${input_text}`);
}
function navigate_to_cart()
{
  navigate(`/cart/${username}`)
}
function navigate_to_home()
{
  navigate(`/home/${username}`)
}
function Log_out()
{
  changeUser("")
  changeVali(false)
  navigate('/')
}


  return (
    <>
    <div className='amazon-header'>     
     <div className='header-search'>
        <input className='header-serchInput' type='text' onKeyDown={(e)=>{if(e.key==='Enter')navigate_user()}} onChange={(e) => setInput_text(e.target.value)} />

     </div>
     <div className='header-nav'>
       
        <div className='header-option' >
        <HomeIcon className='header-icon' onClick={navigate_to_home} />
        </div>
        <div className='header-option cart-option' onClick={navigate_to_cart} >
        <ShoppingCartIcon className='header-icon'/><span>{user_cart?.length}</span>
        </div>
        <div className='header-option'>
          <AccountCircleIcon className='header-icon' onClick={()=>{navigate('/profile')}}/>
        </div>
         <div className='header-option'>
            <LogoutIcon className='header-icon' onClick={Log_out} />
        </div>
     </div>
      
    </div>
    </>
  )
}

export default Header



